import { wrapError } from "./errors";
import { calc } from "./utils";

export async function doSomethingWrongWithCause() {
    try {
        await calc();
    } catch (error) {
        throw new Error("(cause) Something wrong happened!", { cause: error });
    }
}

export async function doSomethingWrongWithWrappedStack() {
    try {
        await calc();
    } catch (error) {
        throw wrapError(new Error("(wrap) Something wrong happened!"), error);
    }
}

export async function doSomethingWrongWithMix() {
    try {
        await calc();
    } catch (error) {
        throw wrapError(new Error("(mix) Something wrong happened!", { cause: error }), error);
    }
}
